            About_Costa_Calma: "O Costa Calma",
            Apartment_Info1: "Witamy na <strong>{{island.props.children}}</strong>  wyspie, ktra zachwyca turystw swoim surowym piknem, zocistymi plaami i krystalicznie czystymi wodami oceanu. Jeli marzysz o idealnych wakacjach, penych soca, relaksu i niezapomnianych widokw, nasze apartamenty <strong>{{colores23.props.children}}</strong> i <strong>{{colores27.props.children}}</strong> w Costa Calma czekaj wanie na Ciebie.",
            Apartment_Info2: "Oferujemy 2 w peni urzdzone apartamenty z tarasem, z miejscem do spania dla 4 osb w kadym.",
            Apartment_Info3: "Apartamenty znajduj si na zamknitym osiedlu z dostpem do basenu oraz sklepem znajdujcym si 20 metrw od wejcia.",
            Apartment_1_description: "Szukasz idealnego miejsca na <strong>wakacje na Fuerteventurze?</strong> Apartament <strong> Colores de Fuerteventura 23</strong> w miejscowoci <strong> Costa Calma</strong> to nowoczesne, przytulne wntrze, ktre zapewnia wyjtkowy komfort i relaks. Po gruntownym remoncie w 2023 roku, apartament oferuje <strong> wysoki standard</strong> wykoczenia, zaprojektowany zmyl o wymagajcych Gociach. Pooony jest w malowniczym kompleksie z basenem i palmami.",
            Apartment_1_description_2: "Apartament Colores de Fuerteventura 23 zlokalizowany na parterze, z <strong> tarasem w cieniu egzotycznych rolin</strong>, gwarantuje spokojny wypoczynek w kameralnej atmosferze. Gocie mog cieszy si urokliwym wntrzem w stylu <strong> boho</strong> - w pastelowych kolorach wyspy. Zaprojektowany zosta przez designera i zyska wyjtkowy wakacyjny, wyspiarski charakter.",
            Apartment_1_description_3: "Apartament Colores de Fuerteventura 23 to doskonaa baza wypadowa do odkrywania pikna wyspy. Pooony w pobliu <strong>play Sotavento</strong>, ktra znajduje si zaledwie 700 metrw od apartamentu, oferuje Gociom szybki dostp do jednej z najpikniejszych pla na wyspie. W bliskiej okolicy znajduje si rwnie centrum handlowe, restauracje, bary oraz lokalny bazar, gdzie mona poczu egzotyczn atmosfer Fuerteventury.",
            Apartment_header_text: "Komfortowy apartament po remoncie  Twj prywatny raj na Fuerteventurze",
            Apartment_header_text_2: "Idealna lokalizacja  blisko play i lokalnych atrakcji",
            Apartment_reservation_header: "Zarezerwuj swj pobyt ju dzi!",
            Apartment_1_reservation_1: "Jeli szukasz spokoju i bliskoci natury, Apartament Colores de Fuerteventura 23 jest idealnym wyborem na Twoje wakacje.",
            Apartment_1_reservation_2: "Fuerteventura czeka na Ciebie  zanurz si w jej wyjtkowej atmosferze i ciesz si niezapomnianymi wakacjami na jednej z najpikniejszych wysp Kanaryjskich.",
            Apartment_2_description: "Szukasz idealnego miejsca na <strong> wakacje na Fuerteventurze?</strong> Apartament <strong> Colores de Fuerteventura 27</strong> w miejscowoci <strong> Costa Calma</strong> to nowoczesne, przytulne wntrze, ktre zapewnia wyjtkowy komfort i relaks. Po gruntownym remoncie w 2023 roku, apartament oferuje <strong> wysoki standard</strong> wykoczenia, zaprojektowany zmyl o wymagajcych Gociach. Pooony jest w malowniczym kompleksie z basenem i palmami.",
            Apartment_2_description_2: "Apartament Colores de Fuerteventura 27 zlokalizowany na pierwszym pitrze, z <strong> tarasem w cieniu egzotycznych rolin</strong>, gwarantuje spokojny wypoczynek w kameralnej atmosferze. Gocie mog cieszy si urokliwym wntrzem w stylu <strong> boho</strong> - w pastelowych kolorach wyspy. Zaprojektowany zosta przez designera i zyska wyjtkowy wakacyjny, wyspiarski charakter.",
            Apartment_2_description_3: "Apartament Colores de Fuerteventura 27 to doskonaa baza wypadowa do odkrywania pikna wyspy. Pooony w pobliu <strong>play Sotavento</strong>, ktra znajduje si zaledwie 700 metrw od apartamentu, oferuje Gociom szybki dostp do jednej z najpikniejszych pla na wyspie. W bliskiej okolicy znajduje si rwnie centrum handlowe, restauracje, bary oraz lokalny bazar, gdzie mona poczu egzotyczn atmosfer Fuerteventury.",
