            Cookies_consent: "Wir mchten Cookies verwenden, um Ihnen eine bequeme Nutzung des Portals <strong>{{webpage.props.children}}</strong> und den Zugang zu den neuesten Verbesserungen zu ermglichen, die wir durch laufende Analysen entwickeln. Wenn Sie der Verwendung aller Cookies ausschlielich zu statistischen Zwecken zustimmen, klicken Sie bitte auf 'Akzeptieren'. Sie knnen Ihre Einwilligung jederzeit widerrufen, indem Sie die Cookie-Einstellungen ndern, Cookies lschen oder Ihre Browsereinstellungen anpassen.",
