            Climate_title: "Das Klima der paradiesischen Insel Fuerteventura",
            Climate_description_1: "Fuerteventura, eine der ltesten Inseln im Archipel der <strong>Kanarischen Inseln</strong>, ist bekannt fr ihr einzigartiges Klima und spektakulre Landschaften. Dank seiner Lage im Atlantischen Ozean, nur 100 Kilometer von der afrikanischen Kste entfernt, geniet die Insel ein mildes, halbwstenartiges Klima mit subtropischem Charakter. Es ist ein Ort, an dem sich natrliche Vielfalt mit auergewhnlichen Wetterbedingungen vereint, die das ganze Jahr ber Touristen aus aller Welt anziehen.",
            Climate_description_2: "Auf <strong>Fuerteventura</strong> gibt es praktisch keine groen Temperaturschwankungen, wobei die durchschnittlichen Werte zwischen 18C im Winter und 30C im Sommer liegen. Charakteristisch fr die Insel sind jedoch die Passatwinde, die die khlende Wirkung des Ozeans mit sich bringen und das Klima auch in den Sommermonaten angenehm mild halten. Diese regelmig aus Nordosten wehenden Winde haben <strong>Fuerteventura</strong> den Spitznamen Insel des Windes eingebracht. Sie sind besonders vorteilhaft fr Wassersportler wie Windsurfer und Kitesurfer und machen Fuerteventura zu einem der besten Orte fr diese Aktivitten.",
            Climate_description_3: "Nachfolgend finden Sie eine detaillierte Beschreibung des Klimas und der Wetterbedingungen in der Stadt <strong>Costa Calma</strong>, unterteilt nach Jahreszeiten, die Ihnen bei der Planung Ihres Aufenthalts entsprechend Ihren Wettervorlieben und Aktivitten hilft.",
            Spring: "Frhling (Mrz - Mai)",
            Summer: "Sommer (Juni - August)",
            Autumn: "Herbst (September - November)",
            Winter: "Winter (Dezember - Februar)",
            Spring_text_1: "<strong>Temperatur:</strong> Im Frhling schwanken die Temperaturen tagsber zwischen 20C und 25C, whrend die Nchte etwas khler sind und etwa 15C - 18C erreichen.",
            Spring_text_2: "<strong>Niederschlag:</strong> Niederschlge sind im Frhling sehr selten, was die Landschaft trocken und halbwstenartig hlt. Gelegentliche leichte Schauer treten zu Beginn des Frhlings auf.",
            Spring_text_3: "<strong>Wind:</strong> Die Passatwinde beginnen in dieser Zeit, insbesondere im Mai, strker zu werden, wodurch die Insel zu einem Paradies fr Surfer, Windsurfer und Kitesurfer wird.",
            Spring_text_4: "<strong>Feuchtigkeit:</strong> Die Luftfeuchtigkeit ist relativ niedrig, was auch bei hheren Temperaturen fr Komfort sorgt.",
            Spring_text_5: "<strong>Meer:</strong> Die Wassertemperatur im Frhling liegt bei etwa 18-20C, was fr einige etwas khl sein kann, aber dennoch zum Schwimmen geeignet ist.",
            Summer_text_1: "<strong>Temperatur:</strong> Im Sommer schwanken die Temperaturen zwischen 25C und 30C, knnen jedoch 30C berschreiten, insbesondere whrend des Einstrmens heier Luft aus der Sahara (das Kalima-Phnomen). Nachts sind die Temperaturen angenehm und liegen zwischen 20C und 24C.",
            Summer_text_2: "<strong>Niederschlag:</strong> Der Sommer auf Fuerteventura ist praktisch niederschlagsfrei, und Niederschlge in dieser Zeit sind eine echte Seltenheit.",
            Summer_text_3: "<strong>Wind:</strong> Die Passatwinde wehen im Sommer am strksten, was die Insel abkhlt und die Hitze viel ertrglicher macht.",
            Summer_text_4: "<strong>Feuchtigkeit:</strong> Im Sommer ist die Luftfeuchtigkeit niedrig, was das Gefhl der Schwle trotz hoher Temperaturen verringert.",
            Summer_text_5: "<strong>Meer:</strong> Die Wassertemperatur des Ozeans steigt auf 22-23C, was ideal zum Schwimmen und fr Wassersportarten ist.",
            Autumn_text_1: "<strong>Temperatur:</strong> Im Herbst sind die Temperaturen sehr mild, mit Tagestemperaturen von 24C bis 28C und Nachttemperaturen zwischen 19C und 22C.",
            Autumn_text_2: "<strong>Niederschlag:</strong> Der Herbst ist die einzige Zeit, in der es zu merklichen Niederschlgen kommen kann, obwohl sie weiterhin selten sind und nicht lange dauern.",
            Autumn_text_3: "<strong>Wind:</strong> Die Herbstwinde schwchen sich im Vergleich zum Sommer ab, was zu etwas ruhigeren Bedingungen fhrt, die ideal fr Touristen sind, die weniger windiges Wetter bevorzugen.",
            Autumn_text_4: "<strong>Feuchtigkeit:</strong> Es ist ein sprbarer Anstieg der Luftfeuchtigkeit zu verzeichnen, die jedoch moderat bleibt.",
            Autumn_text_5: "<strong>Meer:</strong> Die Wassertemperatur im Herbst liegt bei angenehmen 22-24C, was Wassersportler und Badegste anzieht.",
            Winter_text_1: "<strong>Temperatur:</strong> Der Winter auf Fuerteventura ist auergewhnlich mild  die Tagestemperaturen erreichen 19C bis 22C, whrend die Nachttemperaturen auf 15C bis 18C sinken.",
            Winter_text_2: "<strong>Niederschlag:</strong> Dezember und Januar sind Monate, in denen es etwas mehr regnet, obwohl es immer noch eine geringe Menge ist.",
            Winter_text_3: "<strong>Wind:</strong> Im Winter ist der Wind deutlich schwcher als im Sommer, was die Insel ruhiger macht und sie fr Touristen geeigneter macht, die mildere Wetterbedingungen bevorzugen.",
            Winter_text_4: "<strong>Feuchtigkeit:</strong> Die Luftfeuchtigkeit steigt auf ein durchschnittliches Niveau, wodurch das Klima etwas feuchter als im Sommer wird.",
            Winter_text_5: "<strong>Meer:</strong> Die Wassertemperatur des Ozeans sinkt auf etwa 18-20C, aber es ist immer noch mglich, Wassersport zu betreiben und zu schwimmen, obwohl es fr einige khl sein kann.",
            Commuting_info: "Auf dieser Seite findest du detaillierte Informationen zu den verfgbaren Transportmglichkeiten, um unsere Apartments vom Flughafen <strong>Puerto del Rosario</strong> zu erreichen. Unten befindet sich auch eine Flughafenkarte, die dir bei der Orientierung und Planung deiner Reise hilft.",
            Commuting_tiadhe_1: "Zu den Apartments gelangen Sie mit den Stadtbussen von Tiadhe, <strong>Linie 10</strong>. Die Tiadhe-Busse sind <strong>(wei-trkis oder dunkelblau)</strong> gekennzeichnet. Die Bushaltestelle befindet sich vor dem Haupteingang. Der Fahrplan wird auf einem Metallpfosten angebracht.",
            Commuting_tiadhe_2: "Den Link zur Website des Betreibers Tiadhe und den Fahrplan der Linie 10 finden Sie ",
            Commuting_tiadhe_3: "hier",
            Commuting_tiadhe_4: "Der Fahrpreis betrgt ca.",
