            Commuting_shuttle_1: "Nachdem Sie den Hauptkorridor des Flughafens verlassen haben, gehen Sie rechts entlang der Strae, gehen Sie weiter geradeaus am Taxistand vorbei. Die Bushaltestelle befindet sich vor Ihnen - suchen Sie nach <strong>orangenen Bussen</strong>.",
            Commuting_shuttle_2: "Auf der Website des Anbieters",
            Commuting_shuttle_3: "whlen Sie einfach die Adresse der Wohnung, die Flugnummer und die geplante Ankunftszeit. Anschlieend erhalten Sie bis zu 72 Stunden vor Ankunft eine E-Mail mit der Abfahrtszeit des Busses. <strong>Dieser Bus wartet auf seine Passagiere</strong>.",
            Commuting_shuttle_4: "Der Fahrpreis betrgt ca.",
            Commuting_shuttle_5: "7-8  / Person",
            Commuting_taxi_1: "Nachdem Sie den Hauptkorridor des Flughafens verlassen haben, gehen Sie rechts entlang der Strae, die Taxistation ist mit einem Taxischild gekennzeichnet. Hier stehen nur lizenzierte <strong>weie Taxis</strong> zur Verfgung. Leider sind Uber/Bolt-Apps am Flughafen nicht verfgbar.",
            Commuting_taxi_2: "Der Fahrpreis richtet sich nach dem Taxameter.",
            Commuting_taxi_3: "Die Fahrtkosten betragen etwa",
            Commuting_hire_1a: "Im Hauptkorridor des Flughafens gibt es zahlreiche Autovermietungen. Nach der Auswahl des Autos wirst du zum Parkplatz geleitet, der mit dem Buchstaben ",
            Commuting_hire_1b: " auf der obigen Karte gekennzeichnet ist, wo sich dein Fahrzeug befindet.",
            Commuting_hire_2: "<strong>Machen Sie in den ersten Minuten der Anmietung detaillierte Fotos vom Zustand des Fahrzeugs.</strong>",
            Commuting_hire_3: "Die Mietkosten beginnen bei",
